@import url(https://fonts.googleapis.com/css2?family=Oxygen+Mono&display=swap);
@media screen and (max-width: 600px) {
  * {
    font-size: 12px;
  }
  section .Card {
    flex-basis: 100%;
  }
  header {
    display: none !important;
  }
}

body {
  background-color: #f6f4f1;

  background-image: radial-gradient(
      #5a18ae 0.8500000000000001px,
      transparent 0.8500000000000001px
    ),
    radial-gradient(#5a18ae 0.8500000000000001px, #f6f4f1 0.8500000000000001px);
  background-size: 34px 34px;
  background-position: 0 0, 17px 17px;
}
.App {
  display: ;
  color: black;
  font-family: sans-serif;
  text-align: center;

  font-size: 1.2rem;
}
main {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
h1,
h2,
h3 {
  font-family: "Oxygen Mono";
}

h1 {
  font-size: 3rem;
}
li {
  list-style-type: none;
}

header {
  font-size: 1.2rem;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  background-color: #f6f4f1;
  padding: 0 0.5rem;
}

header nav li {
  display: flex;
}
header a {
  text-decoration: none;
  color: black;
  display: block;
  height: 2rem;
}
.Card {
  background: white;
}

.hero {
  padding: 3rem;
  padding-bottom: 7rem;
  background-color: #f6f4f1;
}

section {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.2rem;
  gap: 0.2rem;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  justify-content: space-evenly;
}
section h2 {
  /* flex-basis: 100%; */
  display: block;
  width: 100%;
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: steelblue;
  color: white;
}
section .Card {
  width: 25%;
  border-radius: 1rem;
  border: 1px solid grey;
  /* margin: 0.2rem; */
  padding: 1rem;
  overflow-wrap: break-word;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#projects .Card {
  justify-content: space-between;
}
#projects .Card p {
  flex-grow: 2;
}

section .Card a {
  color: rgba(3, 131, 1);
  border-color: rgba(3, 131, 1, 0.5);
}
#skills .Card {
  box-shadow: 6px 8px 2px 1px rgba(0, 0, 255, 0.2);
}
#experience .Card {
  box-shadow: 6px 8px 1px 1px rgba(186, 186, 0, 0.2);
}
#projects .Card {
  box-shadow: 6px 8px 2px 1px rgba(3, 131, 1, 0.2);
}

footer {
  color: grey;
  margin-top: 4rem;
}
footer a {
  margin: 0 1rem;
}

